import React from 'react';
// import qq from '../../images/fonts/qq.png';
// import wb from '../../images/fonts/wb.png';
// import wx from '../../images/fonts/wx.png';
class Login extends React.Component {
  render () {
    return(
      <div className="wrap">
        <div className="box">
          <form>
            <input type="text" placeholder="/Email"/>
            <input type="text" placeholder=""/>
          </form>
        </div>
        <div className="box1">
          <button onClick={this.props.xxx}></button>
          <button></button>
        </div>
        <p><input type="checkbox" className="inp"/> <span></span></p>
        <div className="box2">
          <a><img src={require('../../images/fonts/wb.png')} /><br/></a>
          <a id="aa"><img src={require('../../images/fonts/wx.png')} /><br/></a>
          <a><img src={require('../../images/fonts/qq.png')} /><br/>QQ</a>
        </div>
      </div>
    )
  }
}

export default Login;



